'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';

export default function Navbar() {
  const [isSticky, setIsSticky] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsSticky(window.scrollY > 0);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <div
      id="sticky-header"
      className={`lovebird_nav_manu${isSticky ? ' sticky-active' : ''}`}
    >
      <div className="container-fluid">
        <div className="row d-flex align-items-center">
          <div className="col-lg-2">
            <div className="logo cursor-scale">
              <Link className="logo_img" href="/" title="lovebird">
                <img src="/assets/images/logo.png" alt="logo" />
              </Link>
              <Link className="main_sticky" href="/" title="lovebird">
                <img src="/assets/images/logo.png" alt="astute" />
              </Link>
            </div>
          </div>
          <div className="col-lg-8">
            <nav className="lovebird_menu">
              <ul className="nav_scroll">
                <li>
                  <a className="cursor-scale" href="#">Home</a>
                  <ul className="sub-menu">
                    <li><Link href="/">Home Version 01</Link></li>
                  </ul>
                </li>
                <li>
                  <a className="cursor-scale" href="#">About</a>
                  <ul className="sub-menu">
                    <li><Link href="/about-us">About Us</Link></li>
                    <li><Link href="/about-me">About Me</Link></li>
                    <li><Link href="/process">Our process</Link></li>
                    <li><Link href="/story">Our story</Link></li>
                    <li><Link href="/gallery">Gallery</Link></li>
                    <li><Link href="/team">Our Team</Link></li>
                    <li><Link href="/team-details">Team Details</Link></li>
                    <li><Link href="/testimonial">Testimonials</Link></li>
                    <li><Link href="/pricing">Pricing</Link></li>
                    <li><Link href="/faq">Faq</Link></li>
                    <li><Link href="/contact">Contact</Link></li>
                  </ul>
                </li>
                <li>
                  <a className="cursor-scale" href="#">Service</a>
                  <ul className="sub-menu">
                    <li><Link href="/service">Service</Link></li>
                    <li><Link href="/service-details">Service Details</Link></li>
                    <li><Link href="/portfolio">Portfolio</Link></li>
                    <li><Link href="/portfolio-details">Portfolio Details</Link></li>
                  </ul>
                </li>
                <li>
                  <a className="cursor-scale" href="#">Shop</a>
                  <ul className="sub-menu">
                    <li><Link href="/shop">Shop</Link></li>
                    <li><Link href="/shop-details">Shop Details</Link></li>
                    <li><Link href="/cart">Cart</Link></li>
                    <li><Link href="/checkout">Checkout</Link></li>
                    <li><Link href="/login">Login</Link></li>
                    <li><Link href="/registration">Registration</Link></li>
                  </ul>
                </li>
                <li>
                  <a className="cursor-scale" href="#">Blog</a>
                  <ul className="sub-menu">
                    <li><Link href="/blog-grid">Blog Grid</Link></li>
                    <li><Link href="/blog-2column">Blog 2Column</Link></li>
                    <li><Link href="/blog-details">Blog Details</Link></li>
                  </ul>
                </li>
                <li>
                  <Link className="cursor-scale" href="/contact">Contact</Link>
                </li>
              </ul>
            </nav>
          </div>
          <div className="col-lg-2">
            <div className="header-btn">
              <Link href="/contact">wedding</Link>
            </div>
          </div>
        </div>
      </div>
    </div>




  );
}
